import React from "react"

import Layout from "@organisms/layout";
import Image from "../components/image"
import SEO from "../components/seo"
import Text from "@particles/text";
import Heading from "@particles/heading";
import SearchBar from "@organisms/search-bar";
import Tags from "@molecules/tags";
import Cards from "@molecules/cards"
import ServiceLogo from "@atoms/service-logo";
import { IconArrowRight } from "@icons";
import Link from "@atoms/link"
import {navigate } from "gatsby"

const IndexPage = () => (
  <Layout>
    <Layout.Center>

      <Heading h1 color={"400"} palette={"primary"}>Welcome!</Heading>
      <Heading h4 background top={1.5} bottom={2}>MetricHQ is your place for everything metrics and KPIs</Heading>
      <div><SearchBar placeholder={"What are you looking for?"}/></div>
      
      <Layout.Row top={1}><Tags prefix={<Text medium>Trending: </Text>} outlined wrap={"true"} tags={["Finance", "Sales", "Marketing", "MRR"]}></Tags></Layout.Row>
      
      <Layout.Grid top={9.5} full min={300} gap={1.5}>
        <Cards.List auto header={"Popular metrics"} footer={<Link to="/">SEE ALL METRICS <IconArrowRight/></Link>}>
          {Array.from({length: 5}, (v,k) => {
            return <Cards.List.Item key={`card-item-${k}`} logo={<ServiceLogo facebook xs/>} action={() => {navigate("/metric")}}><Text color={"200"}>Revenue per Employee</Text> <Text color={"200"}>20 Metrics</Text></Cards.List.Item>
          })}
        </Cards.List>
        <Cards.List auto header={"Popular metric services"} footer={<Link to="/">SEE ALL SERVICES <IconArrowRight/></Link>}>
          {Array.from({length: 5}, (v,k) => {
            return <Cards.List.Item key={`card-item-2-${k}`} logo={<ServiceLogo facebook xs/>}><Text color={"200"}>Facebook</Text> <Text color={"200"}>20 Metrics</Text></Cards.List.Item>
          })}
        </Cards.List>
      </Layout.Grid>

    </Layout.Center>
  </Layout>
)
export default IndexPage
